.header{
    display: flex;
    justify-content: space-between;
    align-items: center;

    box-sizing: border-box;
    position: fixed;
    top: 0;
    width: 100%;
    height: 44px;
    padding: 0 18px;

    border-bottom: 1px solid #dbdbdb;
    background-color: #fff;
}

.headerItemButton{
    cursor: pointer;
}